.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
  position: fixed;
    width: 100%;
    height: 60px;
    bottom: 0;
}

.footer div {
  display: inline-block;
}

.home img {
  height: 450px;
  padding: 30px;
}

.home div {
  cursor: pointer;
}

.cocktailLogo {
  height: 150px;
  width: 150px;
}

.ingridientTable div {
  cursor: pointer;
}

.navHeading {
  color:burlywood;
  font-family: Lucida Handwriting;
  
}

.cocktailDetailImg {
  height: 600px;
  width: 600px;
  vertical-align: top;
}

#table{
  border-collapse: separate;
  border-spacing: 15px;
}

.glassFilter {
  cursor: pointer;
}
